html,body{margin:0;}
*{box-sizing: border-box;}
label,a,span{display:inline-block;}
ul,li{padding:0; margin:0; list-style-type: none;}
h1,h2,h3,figure,p{margin:0;}
main{width:80%; margin:5% auto;}
.btn_parent{text-align:center;}
[type='submit'],[type='button']{
  padding:10px 40px;
  background:#000;
  color:#fff;
  border:none;
}
h1{
  margin:60px 0; font-size:2em; font-weight: normal;
  text-align: center;
}

.add{}
.add ul{}
.add ul li{padding:14px 20px; border-top:1px dashed #ddd;}
.add ul li label{width:10%; vertical-align: middle; color:#777; font-size:12px; font-weight: bold;}
.add ul li input:not([type='submit']){
  width:90%; padding:10px; background:#f3f3f3; border:1px solid #ddd;
  vertical-align: middle;
}
.add ul li textarea{
  width:90%; height:10vw;
  background:#f3f3f3; border:1px solid #ddd;
  vertical-align: middle;
}


.list table{
  border-collapse: collapse;
  width:100%;
}
.list table th{background:#333; color:#fff; padding:12px 0; border-top:1px solid #ddd; border-bottom:1px solid #ddd; font-size:14px;}
.list table td{padding:20px 0; border-bottom:1px dashed #ccc; text-align:center; }
.list table td:nth-child(1){width:7%;}
.list table td:nth-child(2){ width:20%; text-align: left;}
.list table td:nth-child(3){ width:20%;}
.list table td:nth-child(4){}
.list table td:nth-child(5){width:15%;}
.list table tbody tr:nth-of-type(even){
  background:#f5f5f5;
}
.false{display:none}
.true{display:table-row;}


body{background:#f3f3f3;}


@media (max-width:767px) {
  h1{font-size:1.4em; word-break: keep-all; margin:40px 0;}
  main{width:86%;}
  .list table{
    border-collapse: collapse;
    width:100%;
    display:block;
  }
    .list table th{background:#333; color:#fff; padding:12px 0; border-top:1px solid #ddd; border-bottom:1px solid #ddd; font-size:14px;}
  .list table td{padding:20px 0; border-bottom:1px dashed #ccc; text-align:center; }
  .list table td:nth-child(1){width:7%;}
  .list table td:nth-child(2){ width:20%; text-align: left;}
  .list table td:nth-child(3){ width:20%;}
  .list table td:nth-child(4){}
  .list table td:nth-child(5){width:15%;}
  .list table tbody tr:nth-of-type(even){
    background:#f5f5f5;
  }
  .list table thead{display:none;}
  .list table tbody{display: block;}
  .list table tbody tr{
    display:block; border:1px solid #ddd; margin:10% 0; padding:10%;
    background:#fff !important;
    box-sizing: border-box;
  }
  .list table tbody tr td{
    display:block; width:100% !important; 
    border:none; text-align:center !important;
    padding:0 !important; line-height:1.6em;
    
  }
  .list table tbody tr td:nth-child(1){display:none}
  .list table tbody tr td:nth-child(2){
    padding-bottom:20px !important; 
  }
  .list table tbody tr td:nth-child(4) a{
    width:100%; display:block;
    overflow:hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .list table tbody tr td:nth-child(5){
    padding-top:20px !important; 
  }

  
  body{background:#eee;}
  
}